html{
  scroll-padding-top: 98px;
}

.contenedor-principal {
  width: 1903px;
  display: grid;
  grid-template-columns: repeat(12,1fr);
  grid-template-rows: repeat(35,1fr);
  grid-column-gap: 20px;
}

.header-fix{
  width: 1903px;
  height: 98px;
  display: grid;
  grid-template-columns: repeat(12,1fr);
  grid-column-gap: 20px;
