}

.test1-box-card{
  grid-column: 3/5;
  grid-row: 2/5;
  background-color: transparent;
  width: 260px;
  height: 100%;
  perspective: 1000px;
}

.test1-box-inner{
  position: relative;
  width: 260px;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  position: relative;
  top: 40px;
}

.test1-box-card:hover .test1-box-inner{
  transform: rotateY(180deg);
}

.test1-box, .test1-box-back{
  position: absolute;
  width: 260px;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  border-radius: 15px;
}


.img-tes1{
  width: 260px;
  height: 100%;
  border-radius: 15px;
}

.test1-box-back{
  background-color: #F4CE14;
  color: rgb(0, 0, 0);
  border-radius: 15px;
  transform: rotateY(180deg);
}

